.fadeInLeft {
  @include fadeInLeft(
    $duration: .8s,
    // $count: 2,
    // $delay: 1s
    // $function: ease, 
    // $fill: both
  );
}
.fadeInRight {
  @include fadeInRight(
    $duration: .8s,
    // $delay: 1.5s
  );
}
.fadeInUp {
  @include fadeInUp(
    $duration: .8s,
    // $delay: 2s
  );
}
.fadeInDown {
  @include fadeInDown(
    $duration: .8s,
    // $delay: 2s
  );
}
.fadeIn {
  @include fadeIn(
    $duration: 1.5s,
    // $delay: 2s
  );
}
