html,body {
  font-size: 14px;
  @include media-breakpoint-up(md){
    font-size: 16px;
  }
}
.loading {
  position: fixed;
  left: 0px;
  top: 0px;
  width: 100%;
  height: 100%;
  z-index: 9999;
  background: $dark url(../images/svg/Ripple-1s-200px.svg) center no-repeat ;
  background-size: 100px;
}
@mixin bg-cover {
  background-size: cover;
  background-position: center center;
}
.header {
  height: 75vh;
  width: 100%;
  background-color: #fff;
  position: relative;
  overflow: hidden;
  @include media-breakpoint-up(sm){
    height: 100vh;
  }
  @media (orientation:landscape)and(max-height:500px) {
    height: 500px;
  }
}
.logo {
  position: absolute;
  width: 100%;
  z-index: 10;
  margin-top: 1rem;
  >* {
    opacity: 0;
  }
  img {
    margin-left: auto;
    margin-right: auto;
    display: block;
    width: 40%;
    max-width: 215px;
  }
  p {
    text-indent: -9999px;
    margin-bottom: 0;
    margin-left: auto;
    margin-right: auto;
    width: 80%;
    &:nth-child(2) {
      background: url(../images/tit-3.png) center no-repeat;
      background-size: 100% auto;
      height: 2rem;
      max-width: 520px;
    }
    &:nth-child(3) {
      background: url(../images/tit-1.png) center no-repeat;
      background-size: 100% auto;
      height: 5.5rem;
      margin-top: -1.5rem;
      max-width: 545px;
    }
    &:nth-child(4) {
      background: url(../images/tit-2.png) center no-repeat;
      background-size: 100% auto;
      height: 3rem;
      margin-top: -2rem;
      width: 35%;
      max-width: 245px;
    }
    @include media-breakpoint-up(sm) {
      &:nth-child(2) {
        height: 4rem;
      }
      &:nth-child(3) {
        height: 7rem;
      }
      &:nth-child(4) {
        height: 4rem;
      }
    }
  }
  @media (orientation:landscape)and(max-height:500px) {
    width: 55%;
    margin: auto;
    left: 0;
    right: 0;
    top: 0.5rem;
    p {
      &:nth-child(2){
       height: 1.5rem; 
      }
      &:nth-child(3){
        height: 2.5rem;
        margin-top: 0rem;
      }
      &:nth-child(4){
        height: 2rem;
        margin-top: 0;
      }
    }
  }
}
.nav-download {
  position: absolute;
  z-index: 10;
  bottom: .25rem;
  width: 100%;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  a {
    margin-left: .5rem;
    margin-bottom: .5rem;
    padding-left: 2rem;
    padding-right: 2rem;
    width: calc(50% - 0.6rem);
    display: flex;
    align-items: center;
    &:first-child {
      display: none;
    }
    &:nth-child(2) {
      margin-left: 0;
    }
  }
  img {
    max-width: 100%;
    height: auto;
  }
  @include media-breakpoint-up(sm) {
    a {
      width: calc(33% - 0.6rem);
      &:first-child {
        margin-left: 0;
        display: flex;
      }
      &:nth-child(2) {
        margin-left: .5rem;
      }
    }
  }
  @include media-breakpoint-up(lg) {
    a {
      width: auto;
      
    }
  }
}

#scene {
  position: relative;
  height: 100%;
}
.layer {
  position: absolute;
  width: 100%;
  height: 100%;
  .p,.snow,.bg  {
    position: absolute;
    left: -10%;
    opacity: 0;
    transition: all ease-in-out;
    background-repeat: no-repeat;
    background-position: center;
    background-size: auto 100%;
    width: 120%;
    height: 110%;
  }
  .p {
    background-image: url(../images/p.png);
    background-position:center;
  }
  .snow {
    background-image: url(../images/snow.png);
  }
  .bg {
    background-image: url(../images/bg.jpg);
    background-size: cover;
  }
}
.bg {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  &2 {
    background-image: url(../images/bg-1.jpg);
  }
  &-gradien {
    background-image:linear-gradient(0deg, #2c2e41 0%, #3d506b 100%);
  }
  &-gradien-2 {
    background-image:linear-gradient(90deg, #484777 0%, #2f5576 100%);
  }
}

.carousel {
  margin-bottom: 5rem;
  margin-top: 2rem;
  img {
    display: block;
    max-width: 100%;
    margin-right: .5rem;
    transition: opacity 0.4s;
    filter:grayscale(75%);
    &.is-selected {
      filter:grayscale(0%);
    }
    @include media-breakpoint-up(sm) {
      max-width: 50%;
    }
    @include media-breakpoint-up(md) {
      max-width: 33%;
    }
    @include media-breakpoint-up(xl) {
      margin-right: 1rem;
      max-width: 22.5%;
    }
  }
}

.act {
  z-index: 100;
  padding: 2rem 1rem;
  @include media-breakpoint-up(md) {
    position: relative;
  }
  h3 {
    color: $light;
  }
  .tit {
    position: relative;
    padding-left: 7.5rem;
    padding-top: 3.75rem;
    margin-bottom: 1.5rem;
    height: 6rem;
    &::before {
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 7rem;
      height: 100%;
    }
    &.tit-1 {
      &::before {
        background: url(../images/svg/step1.svg) center no-repeat;   
        background-size: auto 100%; 
      }
    }
    &.tit-2 {
      &::before {
        background: url(../images/svg/step2.svg) center no-repeat;   
        background-size: auto 100%; 
      }
    }
    @include media-breakpoint-up(sm) {
      padding-left: 9rem;
      padding-top: 4.5rem;
      height: 7rem;
      &::before {
        width: 9rem;
      }
    }
  }
  >div {
    &:first-child {
      border-bottom: 1px solid $gray-700;
      padding-bottom: 1rem;
    }
  }
  @include media-breakpoint-up(md) {
    >div {
      &:first-child{
        padding-bottom: 0;
        border-right: 1px solid $gray-700;
        border-bottom: none;
      }
    }
  }
}
.btn-gradien {
  color: #7bc8c3;
  background-image:linear-gradient(90deg, #484777 0%, #2f5576 100%);
  border:none;
  font-size: 1.5rem;
  padding: 1rem;
  &:hover {
    color: lighten(#7bc8c3, 10) ;
    background-image:linear-gradient(90deg, lighten(#484777, 15) 0%, lighten(#2f5576, 15) 100%);
  }
  img {
    max-width: 100%;
    height: auto;
  }
}
.tit-bg {
  position: relative;
  margin-top: 4rem;
  &::before {
    content: '';
    position: absolute;
    height: 1.75rem;
    width: 100%;
    top: -2.5rem;
    left: 0;
    background: url(../images/bg-tit-1.png) center no-repeat;
    background-size: auto 100%;
  }
}


/**
*footer區塊內容
**/
footer {
  background: darken($primary, 10);
  color: $light;
  line-height: 1.5em;
}
.company {
  text-align: left;
  font-size: .5em;
  list-style: none;
  padding: 0;
  margin: 0;
  li {
    margin-right: 15px;
  }
}
.icon-f1 {
  background-image: url(../images/svg/f1-logo.svg);
  background-size: auto 100%;
  background-repeat: no-repeat;
  background-position: center;
  width: 130px;
  height: 35px;
}
.icon {
  margin-right: 10px;
  span {
    width: 45px;
    height: 45px;
    display: block;
    background-repeat: no-repeat;
    background-position: center;
  }
  &-12 {
    background-image: url(../images/12.png);
  }
  &-15 {
    background-image: url(../images/15.png);
  }
}
.grade {
  line-height: 1.3em;
  font-size: 0.8em;
  list-style: none;
  padding: 0;
  margin: 0;
}
